import React from 'react';
import { Card, Button, Modal } from 'antd';

import MyClientsTable from 'components/clients/MyClientsTable';
import CreateClientForm from 'components/clients/CreateClientForm';

const Clients = () => {

    let [create, setCreate] = React.useState(false);

    return (
        <>
            <Modal
                title="Create Client"
                visible={create}
                footer={null}
                onCancel={() => setCreate(false)}
                width={1000}
                destroyOnClose
            >
                <CreateClientForm setCreate={setCreate} />
            </Modal>
            <Card>
                <div style={styles.header}>
                    <h2>Clients</h2>
                    <Button
                        type="primary"
                        htmlType="submit"
                        color='red-1'
                        onClick={() => setCreate(true)}
                    >
                        Create Client
                    </Button>
                </div>
            </Card>
            <Card>
                <MyClientsTable />
            </Card>
        </>
    )
}

export default Clients;

const styles = {
    header: {
        display: 'flex',
        padding: '0px  40px',
        flexDirection: 'row',
        justifyContent: 'space-between'
    }
}