import React from 'react';
import { Row, Col, Avatar, Button, Select, Form, Input, message } from 'antd';
import { UserOutlined, UploadOutlined } from '@ant-design/icons';
import axios from 'axios';
import { connect } from 'react-redux';

let { Option } = Select;

const EditClientForm = (props) => {

    let { user_id, clientInfo, setEdit } = props;

    let [locationList, setLocationList] = React.useState([]);

    const getCurrencyAndLocation = async () => {
        let res = await axios.post('/locationAndCurrency', { user_id: 1 });
        setLocationList(res.data.location);
    }

    const editClient = async (values) => {
        try {
            console.log(values);
            let res = await axios.post('/updateclient', { ...values, user_id, client_id: clientInfo.client_id });
            setEdit(false);
            message.success(res.data.message);

        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const onFinishFailed = errorInfo => {
        console.log('Failed:', errorInfo);
    };

    React.useEffect(() => {
        getCurrencyAndLocation();
    }, [])

    return (
        <div>
            <Form
                name="basicInformation"
                layout="horizontal"
                onFinish={editClient}
                onFinishFailed={onFinishFailed}
            >
                <Row style={{ justifyContent: 'center' }}>
                    <Col xs={24} sm={24} md={24} lg={20} >
                        <Row gutter={16} align="center">
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Contact Person"
                                    name="contactperson"
                                    initialValue={clientInfo.client_contactperson}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Company Name"
                                    name="companyname"
                                    initialValue={clientInfo.client_companyname}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Office Number"
                                    name="officenumber"
                                    initialValue={clientInfo.client_officenumber}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Alternate Number"
                                    name="alternateofficenumber"
                                    initialValue={clientInfo.client_alternateofficenumber}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Address"
                                    name="address"
                                    initialValue={clientInfo.client_address}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="State"
                                    name="state"
                                    initialValue={clientInfo.client_state}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="City Office Number"
                                    name="city"
                                    initialValue={clientInfo.client_city}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={12}>
                                <Form.Item
                                    label="Country"
                                    name="country"
                                    initialValue={clientInfo.location_name}
                                    rules={[{
                                        required: true,
                                        message: 'Select Campaign Location'
                                    }]}
                                >
                                    <Select >
                                        {
                                            locationList.map(item => {
                                                return (
                                                    <Option key={item.location_id} value={item.location_id}>
                                                        {item.location_name}
                                                    </Option>

                                                )
                                            })
                                        }
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Timezone"
                                    name="timezone"
                                    initialValue={clientInfo.client_timezone}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Email"
                                    name="email"
                                    initialValue={clientInfo.client_email}
                                    rules={[
                                        {
                                            required: true,
                                            type: "email",
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Alternate Email"
                                    name="alternateemail"
                                    initialValue={clientInfo.client_alternateemail}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Webiste"
                                    name="webiste"
                                    initialValue={clientInfo.client_webiste}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Company Industry"
                                    name="companyindustry"
                                    initialValue={clientInfo.client_companyindustry}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Designation"
                                    name="designation"
                                    initialValue={clientInfo.client_designation}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Zipcode"
                                    name="zipcode"
                                    initialValue={clientInfo.client_zipcode}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={8}>
                                <Form.Item
                                    label="Total Revenue"
                                    name="totalrevenue"
                                    initialValue={clientInfo.client_totalrevenue}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={8} >
                                <Form.Item
                                    label="Twitter"
                                    name="twitterid"
                                    initialValue={clientInfo.client_twitterid}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={8} >
                                <Form.Item
                                    label="Facebook"
                                    name="facebookid"
                                    initialValue={clientInfo.client_facebookid}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={8} >
                                <Form.Item
                                    label="Instagram"
                                    name="instagramid"
                                    initialValue={clientInfo.client_instagramid}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={16} >
                                <Form.Item
                                    label="Company Description"
                                    name="companydescription"
                                    initialValue={clientInfo.client_companydescription}
                                    rules={[
                                        {
                                            required: true,
                                            message: 'Please input Campaign Name!',
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                            </Col>
                        </Row>
                        <Button type="primary" htmlType="submit" style={{ float: 'right' }}>
                            Save
                        </Button>
                    </Col>
                </Row>
            </Form>
        </div >
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id
});

export default connect(mapStateToProps)(EditClientForm);