import React from 'react';
import { Card, Table, Tooltip, message, Button, Col, Row, Popconfirm, Modal, Input } from 'antd';
import Highlighter from 'react-highlight-words';
import { EyeOutlined, DeleteOutlined, FileDoneOutlined, SearchOutlined } from '@ant-design/icons';
import axios from 'axios';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';

import EditClientForm from './EditClientForm';


const MyClientsTable = (props) => {
    let { user_id, role_id, } = props;
    let history = useHistory();
    let [data, setData] = React.useState([]);
    let [loading, setLoading] = React.useState(true);
    let [edit, setEdit] = React.useState(false);
    let [clientInfo, setClientInfo] = React.useState({}); //single client info for edit

    const getClientList = async () => {
        try {
            let body = {
                user_id,
                role_id
            }
            let res = await axios.post('/clientlist', body);
            setData(res.data.data);
            setLoading(false);
        } catch (error) {
            setLoading(false);
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message);
        }
    }

    const onEditClick = async (client_id) => {
        try {
            let res = await axios.post('/clientdetails', { user_id, client_id });
            setClientInfo(res.data.data);
            setEdit(true);
        } catch (error) {
            console.log(error);
        }
    }

    const deleteBtnClick = async (client_id) => {
        try {
            let res = await axios.post('/deleteclient', { user_id, client_id });
            message.success(res.data.message);
        } catch (error) {
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    //search work
    let searchInput = React.createRef();
    let [state, setState] = React.useState({});
    const getColumnSearchProps = dataIndex => ({
        filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
            <div style={{ padding: 8 }}>
                <Input
                    ref={searchInput}
                    placeholder={`Search ${dataIndex}`}
                    value={selectedKeys[0]}
                    onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
                    onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
                    style={{ width: 188, marginBottom: 8, display: 'block' }}
                />
                <Button
                    type="primary"
                    onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
                    icon={<SearchOutlined />}
                    size="small"
                    style={{ width: 90, marginRight: 8 }}
                >
                    Search
            </Button>
                <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
                    Reset
            </Button>
            </div>
        ),
        filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
        onFilter: (value, record) =>
            record[dataIndex]
                .toString()
                .toLowerCase()
                .includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: visible => {
            if (visible) {
                setTimeout(() => searchInput.current?.select());
            }
        },
        render: text =>
            state.searchedColumn === dataIndex ? (
                <Highlighter
                    highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
                    searchWords={[state.searchText]}
                    autoEscape
                    textToHighlight={text.toString()}
                />
            ) : (
                    text
                ),
    });

    const handleSearch = (selectedKeys, confirm, dataIndex) => {
        confirm();
        setState({
            ...state,
            searchText: selectedKeys[0],
            searchedColumn: dataIndex,
        });
    };

    const handleReset = clearFilters => {
        clearFilters();
        setState({ ...state, searchText: '' });
    };

    React.useEffect(() => {
        getClientList();
    }, []);

    const columns = [
        {
            title: 'Client Name',
            dataIndex: 'client_contactperson',
            align: 'center',
            sorter: true,
            sortDirections: ['ascend', 'descend', 'ascend'],
            ...getColumnSearchProps('client_contactperson'),
        },
        {
            title: 'Company',
            dataIndex: 'client_companyname',
            align: 'center',
            ...getColumnSearchProps('client_companyname'),
        },
        {
            title: 'Client Email',
            dataIndex: 'client_email',
            align: 'center',
            ...getColumnSearchProps('client_email'),
        },
        {
            title: 'Client Contact',
            dataIndex: 'client_officenumber',
            align: 'center',
            ...getColumnSearchProps('client_officenumber'),
        },
    ];

    const admin = [
        ...columns,
        {
            title: 'Created By',
            dataIndex: 'user_name',
            align: 'center',
        },
        {
            title: 'Campaign Name',
            dataIndex: 'campaign_campaignname',
            align: 'center',
            sorter: (a, b) => {
                a = a.user_name.toLowerCase();
                b = b.user_name.toLowerCase();
                return a > b ? -1 : b > a ? 1 : 0;
            },
        },
        {
            title: 'Actions',
            dataIndex: 'actions',
            align: 'center',
            render: (_, record) => (
                <div className="text-center">
                    <Tooltip title="Edit">
                        <Button
                            type="primary"
                            className="mr-2"
                            icon={<EyeOutlined />}
                            size="small"
                            onClick={() => onEditClick(record.client_id)}
                        />
                    </Tooltip>
                    <Popconfirm
                        placement="topRight"
                        title={'Are you sure, you want to delete this user? '}
                        onConfirm={() => deleteBtnClick(record.client_id)}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Tooltip title="Remove">
                            <Button danger icon={<DeleteOutlined />} size="small" />
                        </Tooltip>
                    </Popconfirm>
                </div>
            )
        }
    ];

    const notAdmin = [
        ...columns,
        {
            title: 'Actions',
            dataIndex: 'actions',
            align: 'center',
            render: (_, record) => (
                <div className="text-center">
                    <Tooltip title="Create Order">
                        <Button
                            type="primary"
                            icon={<FileDoneOutlined />}
                            className="mr-2"
                            size="small"
                            style={{ background: 'green', borderColor: 'green' }}
                            onClick={() => history.push('/orders/form', { client_id: record.client_id })}
                        />
                    </Tooltip>
                    <Tooltip title="Edit">
                        <Button
                            type="primary"
                            className="mr-2"
                            icon={<EyeOutlined />}
                            size="small"
                            onClick={() => onEditClick(record.client_id)}
                        />
                    </Tooltip>
                    <Popconfirm
                        placement="topRight"
                        title={'Are you sure, you want to delete this user? '}
                        onConfirm={() => deleteBtnClick(record.client_id)}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Tooltip title="Remove">
                            <Button danger icon={<DeleteOutlined />} size="small" />
                        </Tooltip>
                    </Popconfirm>
                </div>
            )
        }
    ];

    return (
        <>
            <Modal
                title="Edit Client"
                visible={edit}
                footer={null}
                onCancel={() => setEdit(false)}
                width={1000}
                destroyOnClose
            >
                <EditClientForm clientInfo={clientInfo} setEdit={setEdit} />
            </Modal>
            <Row style={{ justifyContent: 'center' }}>
                <Col lg={22} >
                    <Card bodyStyle={{ 'padding': '0px' }}>
                        <Table
                            columns={role_id == 1 ? admin : notAdmin}
                            dataSource={data}
                            rowKey='client_id'
                            loading={loading}
                            pagination={false}
                        />
                    </Card>
                </Col>
            </Row>
        </>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id,
    role_id: state.auth.user.role_id
});

export default connect(mapStateToProps)(MyClientsTable);